const Projects = () => {
  const [activeFilter, setActiveFilter] = useState('all');
  const [filteredProjects, setFilteredProjects] = useState(projects);
  const [selectedProject, setSelectedProject] = useState(null);
  const [ref, inView] = useInView({
    triggerOnce: true,
    threshold: 0.1
  });
  
  useEffect(() => {
    if (activeFilter === 'all') {
      setFilteredProjects(projects);
    } else {
      setFilteredProjects(projects.filter(project => project.category === activeFilter));
    }
  }, [activeFilter]);
  
  const handleFilterClick = (filter) => {
    setActiveFilter(filter);
  };
  
  const openModal = (project) => {
    setSelectedProject(project);
    document.body.style.overflow = 'hidden';
  };
  
  const closeModal = () => {
    setSelectedProject(null);
    document.body.style.overflow = 'auto';
  };
  
  //    
  const categories = ['all', ...new Set(projects.map(project => project.category))];
  
  const cardVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: (i) => ({
      opacity: 1,
      y: 0,
      transition: {
        delay: i * 0.1,
        duration: 0.5
      }
    })
  };
  
  return (
    <ProjectsContainer>
      <SectionTitle>
         <span className="accent"></span>
      </SectionTitle>
      
      <FilterContainer>
        {categories.map(category => (
          <FilterButton
            key={category}
            $active={activeFilter === category}
            onClick={() => handleFilterClick(category)}
          >
            {category === 'all' ? '' : category}
          </FilterButton>
        ))}
      </FilterContainer>
      
      <ProjectsGrid ref={ref}>
        {filteredProjects.map((project, index) => (
          <ProjectCard
            key={project.id}
            onClick={() => openModal(project)}
            variants={cardVariants}
            initial="hidden"
            animate={inView ? "visible" : "hidden"}
            custom={index}
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            <ProjectImage className="project-image" />
            
            <AsciiOverlay className="ascii-overlay">
              {generateProjectAscii(index)}
            </AsciiOverlay>
            
            <ProjectInfo className="project-info">
              <ProjectTitle>{project.title}</ProjectTitle>
              <ProjectCategory>{project.category}</ProjectCategory>
              <ProjectDescription>{project.description}</ProjectDescription>
            </ProjectInfo>
          </ProjectCard>
        ))}
      </ProjectsGrid>
      
      <AnimatePresence>
        {selectedProject && (
          <ModalBackdrop
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={closeModal}
          >
            <ModalContent
              onClick={(e) => e.stopPropagation()}
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
            >
              <CloseButton onClick={closeModal}></CloseButton>
              
              <ModalHeader>
                <ModalTitle>{selectedProject.title}</ModalTitle>
                <ModalCategory>{selectedProject.category}</ModalCategory>
              </ModalHeader>
              
              <ModalBody>
                <ModalImageContainer>
                  <ModalImage />
                </ModalImageContainer>
                
                <ModalDescription>{selectedProject.longDescription}</ModalDescription>
                
                <ModalDetails>
                  <DetailRow>
                    <DetailLabel>:</DetailLabel>
                    <DetailValue>{selectedProject.client}</DetailValue>
                  </DetailRow>
                  <DetailRow>
                    <DetailLabel>:</DetailLabel>
                    <DetailValue>{selectedProject.year}</DetailValue>
                  </DetailRow>
                  <DetailRow>
                    <DetailLabel>:</DetailLabel>
                    <DetailValue>{selectedProject.role}</DetailValue>
                  </DetailRow>
                </ModalDetails>
              </ModalBody>
            </ModalContent>
          </ModalBackdrop>
        )}
      </AnimatePresence>
    </ProjectsContainer>
  );
};

export default Projects; 